var fieldTemperature = document.getElementById('temperature');
var fieldHumidity = document.getElementById('humidity');
var fieldCO = document.getElementById('co');

var PERIOD = 3000;
var TEMPERATURE = 37;
var HUMIDITY = 58;
var CO1 = 0.5;
var CO2 = 1.5;

function getRandomFloat() {
    return Number(Math.random().toFixed(1));
}

function getRandomInterval(min, max) {
    return (Math.random() * (max - min) + min).toFixed(1);
}

function setRandomValue(elem, data, interval) {
    elem.textContent = interval ? getRandomInterval(CO1, CO2) : data + getRandomFloat();
}

function setIntervalIteration() {
    setInterval(function () {
        setRandomValue(fieldTemperature, TEMPERATURE)
    }, PERIOD);
    setInterval(function () {
        setRandomValue(fieldHumidity, HUMIDITY)
    }, PERIOD);
    setInterval(function () {
        setRandomValue(fieldCO, null, true)
    }, PERIOD);
}

// Date
function displayDate() {
    var dateOptions = {
        year: 'numeric',
        month: 'long',
        day: 'numeric',
        timezone: 'UTC',
        hour: 'numeric',
        minute: 'numeric'
    };

    setInterval(function () {
        var dateContainer = document.getElementById('date');
        dateContainer.textContent = new Date().toLocaleString('ru', dateOptions);
    }, 1000)
}

displayDate();
setIntervalIteration();

// Modal
var form = document.querySelector('.settings');
var passwordInput = form.querySelector('input');
var passwordLabel = form.querySelector('label');

passwordInput.addEventListener('focus', function () {
    passwordLabel.classList.add('active');
});

passwordInput.addEventListener('focusout', function () {
    passwordLabel.classList.remove('active');
});

// MagnificPopup plugin (plus Zepto)
$('.btn_events').magnificPopup({
    items: {
        src: '#modal-events',
        type: 'inline'
    }
});

$('.btn_settings').magnificPopup({
    items: {
        src: '#modal-settings',
        type: 'inline'
    }
});

$('.btn_close').click(function () {
    $.magnificPopup.close();
    passwordInput.value = '';
});
            